* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App {
  width: 800px;
}
.post {
  display: flex;
  padding: 10px;
  border: 2px solid rebeccapurple;
  margin-top: 15px;
  justify-content: space-between;
  align-items: center;
}
.post__btns {
  display: flex;
}
.post-enter {
  transform: translateX(-50px);
}
.post-enter-active {
  transform: translateX(0px);
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(-50px);
  transition: all 500ms ease-in;
}
.page__wrapper {
  margin-top: 30px;
}

.page {
  padding: 10px;
  border: 1px solid rebeccapurple;
  cursor: pointer;
}
.page__current {
  border: 2px solid rgb(4, 146, 127);
  font-weight: bold;
}
.navbar {
  height: 50px;
  width: 100vw;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background: lightblue;
}
.navbar__links {
  margin-left: auto;
}
